@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Coming+Soon|Fredoka+One|Montserrat&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit;outline:none}body{margin:0;overflow:hidden}.App{position:relative}.hud{display:grid;grid-template-columns:1fr 1fr 1fr;padding:2%}a{text-decoration:none}.btn-help{border:3px solid #fff;background:#fce01a;box-shadow:0 -2px 0 3px #f1d403 inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;display:inline-block;color:#f8feff;text-align:center;font-family:"Fredoka One";text-decoration:none;border-radius:100%;height:70px;width:70px;line-height:3.7rem;font-size:3rem;margin:2% 0 0 90%;cursor:pointer}.btn-start{border:3px solid #fff;background:#f4863d;box-shadow:0 -2px 0 3px #f2721d inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;display:inline-block;color:#f8feff;text-align:center;font-family:"Fredoka One";text-decoration:none;border-radius:20px;height:3rem;width:15rem;line-height:2.5rem;font-size:1.4rem;cursor:pointer}.btn-newstart{border:3px solid #fff;background:#8f8aef;box-shadow:0 -2px 0 3px #605ad7 inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;display:inline-block;color:#f8feff;text-align:center;font-family:"Fredoka One";text-decoration:none;border-radius:20px;height:3rem;width:15rem;line-height:2.5rem;font-size:1.4rem;cursor:pointer;margin-top:1rem}.btn-back,.btn-play{border:3px solid #fff;background:#5f59d0;box-shadow:0 -2px 0 3px #726ceb inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;display:inline-block;color:#f8feff;text-align:center;font-family:"Fredoka One";text-decoration:none;border-radius:20px;height:3rem;width:9rem;line-height:2.5rem;font-size:1.4rem;margin:2%;justify-self:start;cursor:pointer}.btn-back p,.btn-start p,.btn-help p{margin:0}.btn-back:active,.btn-start:active,.btn-help:active{-webkit-filter:brightness(120%);filter:brightness(120%);-webkit-transform:scale(0.95);transform:scale(0.95)}.signpost{display:inline-block;color:#f8feff;text-align:center;font-family:"Fredoka One";background:#186fc6;border:3px solid #fff;border-radius:10px;box-shadow:0 -2px 0 3px #3c91e6 inset,0 2px 2px rgba(60,145,230,0.17),0 10px rgba(255,255,255,0.25) inset;min-height:3rem;line-height:2rem;font-size:1.4rem;padding:1% 5%;margin:2%}@media only screen and (max-height: 600px){html{font-size:12px}}

.helper-avatar-container{position:absolute;justify-self:flex-end;height:30vh;right:15px;bottom:-8vh;display:flex;align-items:center}.helper-avatar{margin-left:1vw;height:30vh}.speech-box{background:linear-gradient(to bottom, #69dfc7 0%, #69dfc7 20%, #69dfc7 80%, #69dfc7 100%);border:solid 2px white;border-radius:2em;min-width:25rem;align-items:center;padding:3%;position:relative}.speech-box div,.speech-box p{text-align:center;margin:0;font-family:"Amatic SC", cursive;font-weight:bold;color:#0a2e27;font-size:1.5rem}.speech-box:after,.speech-box:before{content:" ";height:0;pointer-events:none;position:absolute;top:3em;width:0}.speech-box:after{background:#69dfc7;border:solid white;border-radius:0 2.6em 1.6em 0;border-width:0 0 2px 2px;height:0.65em;left:100.1%;top:55%;margin:-0.62em 0 0 -1.5px;width:0.8em}.speech-box:before{background:#fde74c;border:solid white;border-radius:0 1.6em 1.6em 0;border-width:0px 3px 2px 0px;height:1em;top:80%;left:100.1%;margin:-1.3em 0 0 -1px;width:0.8em}

.background-home{background-image:url(/img/Background_Home.svg);min-height:100vh;background-position:center;background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;justify-content:center}.container-home{display:flex;flex-direction:column;align-items:center}.img-logo{width:60vw;padding-bottom:1%}

.background-help{min-height:100vh;background-position:center;background-repeat:no-repeat;background-size:cover}.video-container{display:flex;flex-direction:column;margin:0px auto;width:60vw}.video-player{align-self:center;width:100%;height:auto;border:white 8px solid;background-color:white}.btn-play{align-self:center}.btn-play.play{background-color:#f69a5d;box-shadow:0 -2px 0 3px #f69a5d inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset}

.map-background{height:100vh;background-position:center;background-repeat:no-repeat;background-size:cover}

.place-circle{margin:0 auto;display:flex;flex-direction:column;align-items:center;justify-content:center;pointer:cursor}.place-circle.unlocked p{background:#f69a5d;box-shadow:0 -2px 0 3px #e8894a inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;border-radius:10px;min-width:15vw}.place-circle.locked p{background:#8f8aef;box-shadow:0 -2px 0 3px #6f69eb inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;border-radius:10px;min-width:15vw;border-radius:10px}.place-circle.locked .icon-image{-webkit-filter:grayscale(90%);filter:grayscale(90%)}.place-circle p{font-family:"Fredoka One", cursive;text-align:center;font-size:1rem;margin:2%;color:#04121f;background-color:#f69a5d;border-radius:10px;padding:3%}.place-circle img{max-width:20vw}.lock-icon{width:10vw;z-index:1}.router-link a{display:flex;flex-direction:column;align-items:center}.stars svg{width:3vw}

.interaction-circle{height:20vh;margin:0 auto;display:flex;flex-direction:column;align-items:center;justify-content:center;pointer:cursor}.interaction-circle.unlocked p{background:#f69a5d;box-shadow:0 -2px 0 3px #e8894a inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;border-radius:10px;min-width:15vw}.interaction-circle.unlocked.completed p{background:#fde74c;box-shadow:0 -2px 0 3px #f3d80d inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset}.interaction-circle.locked p{background:#8f8aef;box-shadow:0 -2px 0 3px #6f69eb inset,0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;border-radius:10px;min-width:15vw;border-radius:10px}.interaction-circle.locked .icon-image{-webkit-filter:grayscale(90%);filter:grayscale(90%)}.interaction-circle p{font-family:"Fredoka One", cursive;text-align:center;font-size:1.1rem;margin:2%;color:#04121f;background-color:#f69a5d;border-radius:10px;padding:3%}.router-link a{display:flex;flex-direction:column;align-items:center}.interaction-circle:active{-webkit-transform:scale(75%);transform:scale(75%)}.interaction-circle:hover{height:21vh}.stars-container{justify-self:end;width:45%}.stars{margin-top:2px;display:flex;justify-content:space-around}

.interaction-grid{margin:2% auto;padding:0 !important;min-height:40vh;width:90vw;display:grid;grid-template-columns:1fr 2fr 1fr;align-items:stretch;justify-items:center}.interaction-question{text-align:center;font-family:"Montserrat", sans-serif;font-size:2rem;font-weight:bold;margin:1% 0 0 0;color:#332d2d;width:55%;margin-left:20%;padding:1%;background-color:#8fe8b5b3;margin-top:-1%}.interaction-instruction{text-align:center;font-family:"Amatic SC", cursive;font-weight:600;font-size:2rem;margin:0}.btn-answer{border:3px solid #f69a5d;background:#feee7e;box-shadow:0 5px 5px rgba(65,8,8,0.17),0 15px rgba(255,255,255,0.25) inset;display:inline-block;color:#0a2e27;text-align:center;font-family:"Montserrat", sans-serif;text-decoration:none;cursor:pointer;border-radius:20px;width:100%;margin:1.5vh 0;font-size:1.2rem;padding:0.9rem 0}.selected{border:3px solid #30731e;background:#7dd764}.grid-center{grid-column-start:2;grid-row-start:1;grid-row-end:5}.interaction-image{border-radius:20px;border:8px solid #fbf5e7;height:50vh;box-shadow:2px 2px 10px #2b2626}.star-popup{display:flex;align-items:center;justify-content:center;width:40vw;margin:auto;margin-top:-5px;font-family:"Amatic SC", cursive;font-size:2rem;text-align:center;font-weight:700}.star-popup svg{margin:0 1vw}@media only screen and (max-height: 600px){.btn-answer,.interaction-instruction{font-size:1rem}.interaction-question{font-size:1.5rem}.btn-answer{margin:1vh 0;padding:0.3rem 0}.star-popup{font-size:1rem}.star-popup svg{height:6vh;width:6vh}}

.fourofour{margin:none;min-height:100vh;min-width:100vw;background-color:#80ef99;display:flex;align-items:center;justify-content:center}.fourofour h1{font-size:3em;color:#8f8aef;font-family:"Fredoka One";margin-top:-1em}

